/* retire-lah-client/src/index.css */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply bg-brand-bg text-brand-dark-text font-sans antialiased tracking-tight selection:bg-brand-primary selection:text-white;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  h1, h2, h3, h4, h5, h6 {
    @apply font-semibold text-brand-dark-text;
  }
  h1 { @apply text-3xl sm:text-4xl md:text-5xl; }
  h2 { @apply text-xl sm:text-2xl md:text-3xl; }
  h3 { @apply text-lg sm:text-xl md:text-2xl; }
  p { @apply text-brand-medium-text leading-relaxed; }
}

@layer components {
  /* Form Element Base Styles */
  .form-input,
  .form-select {
    @apply block w-full rounded-lg border-brand-border shadow-subtle 
           focus:border-brand-primary-light focus:ring-2 focus:ring-brand-primary focus:ring-opacity-40 
           text-sm sm:text-base py-2.5 px-3.5 placeholder-brand-light-text placeholder-opacity-70;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  }
  .form-select {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%236B7280' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='M6 8l4 4 4-4'/%3e%3c/svg%3e");
    background-position: right 0.75rem center;
    background-repeat: no-repeat;
    background-size: 1.25em 1.25em;
    padding-right: 2.75rem;
  }
  label { /* General label styling */
    @apply block text-xs sm:text-sm font-semibold text-brand-medium-text mb-1.5;
  }

  /* Button Base Styles */
  .btn {
    @apply font-semibold py-2.5 px-5 rounded-lg shadow-subtle
           tracking-wide text-sm sm:text-base
           transition-all duration-150 ease-in-out transform 
           focus:outline-none focus:ring-2 focus:ring-offset-2 
           disabled:opacity-50 disabled:cursor-not-allowed disabled:shadow-none disabled:hover:brightness-100;
  }
  .btn-primary { @apply btn bg-brand-primary text-white hover:bg-brand-primary-dark active:scale-[0.98] focus:ring-brand-primary; }
  .btn-secondary { @apply btn bg-brand-secondary text-white hover:bg-brand-secondary-dark active:scale-[0.98] focus:ring-brand-secondary; }
  .btn-accent { @apply btn bg-brand-accent text-white hover:bg-amber-600 active:scale-[0.98] focus:ring-brand-accent; }
  .btn-outline { @apply btn bg-transparent border-2 hover:bg-opacity-10 active:bg-opacity-20; }
  .btn-outline-primary { @apply btn-outline text-brand-primary border-brand-primary hover:bg-brand-primary; }
  .btn-outline-secondary { @apply btn-outline text-brand-secondary border-brand-secondary hover:bg-brand-secondary; }

  /* Tooltip Styles - Group Hover Version */
  .tooltip-trigger { 
    /* 'group' class MUST be applied in JSX/HTML, NOT here with @apply */
    @apply relative inline-block cursor-help; 
  }
  .tooltip-content { 
    @apply absolute z-30 w-max max-w-xs px-3 py-2 text-xs font-medium text-white bg-gray-800 rounded-lg shadow-xl 
           opacity-0 invisible group-hover:opacity-100 group-hover:visible 
           transition-opacity duration-200 ease-in-out;
    bottom: 135%; 
    left: 50%;
    transform: translateX(-50%);
    pointer-events: none; 
  }
  .tooltip-content::after {
    content: "";
    position: absolute;
    top: 100%; 
    left: 50%;
    margin-left: -5px;
    border-width: 6px;
    border-style: solid;
    border-color: #1f2937 transparent transparent transparent; /* bg-gray-800 */
  }
}

/* Scrollbar styles */
::-webkit-scrollbar { width: 8px; height: 8px; }
::-webkit-scrollbar-track { background: #f9fafb; border-radius: 10px; }
::-webkit-scrollbar-thumb { background: #d1d5db; border-radius: 10px; }
::-webkit-scrollbar-thumb:hover { background: #9ca3af; }